import { showError } from '@nextcloud/dialogs';
import { confirmPassword } from '@nextcloud/password-confirmation';
import NcButton from '@nextcloud/vue/dist/Components/NcButton.js';
import NcTextField from '@nextcloud/vue/dist/Components/NcTextField.js';
import logger from "../../logger.ts";
import { startRegistration, finishRegistration } from "../../service/WebAuthnRegistrationSerice.ts";
import '@nextcloud/password-confirmation/dist/style.css';
const logAndPass = text => data => {
  logger.debug(text);
  return data;
};
const RegistrationSteps = Object.freeze({
  READY: 1,
  REGISTRATION: 2,
  NAMING: 3,
  PERSIST: 4
});
export default {
  name: 'AddDevice',
  components: {
    NcButton,
    NcTextField
  },
  props: {
    httpWarning: Boolean,
    isHttps: {
      type: Boolean,
      default: false
    },
    isLocalhost: {
      type: Boolean,
      default: false
    }
  },
  setup() {
    // non reactive props
    return {
      RegistrationSteps
    };
  },
  data() {
    return {
      name: '',
      credential: {},
      step: RegistrationSteps.READY
    };
  },
  watch: {
    /**
     * Auto focus the name input when naming a device
     */
    step() {
      if (this.step === RegistrationSteps.NAMING) {
        this.$nextTick(() => this.$refs.nameInput?.focus());
      }
    }
  },
  methods: {
    /**
     * Start the registration process by loading the authenticator parameters
     * The next step is the naming of the device
     */
    async start() {
      this.step = RegistrationSteps.REGISTRATION;
      console.debug('Starting WebAuthn registration');
      try {
        await confirmPassword();
        this.credential = await startRegistration();
        this.step = RegistrationSteps.NAMING;
      } catch (err) {
        showError(err);
        this.step = RegistrationSteps.READY;
      }
    },
    /**
     * Save the new device with the given name on the server
     */
    submit() {
      this.step = RegistrationSteps.PERSIST;
      return confirmPassword().then(logAndPass('confirmed password')).then(this.saveRegistrationData).then(logAndPass('registration data saved')).then(() => this.reset()).then(logAndPass('app reset')).catch(console.error);
    },
    async saveRegistrationData() {
      try {
        const device = await finishRegistration(this.name, this.credential);
        logger.info('new device added', {
          device
        });
        this.$emit('added', device);
      } catch (err) {
        logger.error('Error persisting webauthn registration', {
          error: err
        });
        throw new Error(t('settings', 'Server error while trying to complete WebAuthn device registration'));
      }
    },
    reset() {
      this.name = '';
      this.registrationData = {};
      this.step = RegistrationSteps.READY;
    }
  }
};